import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';

import { Link } from 'react-router-dom'

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function TemporaryDrawerU() {
  const classes = useStyles();
  const [state, setState] = React.useState({
  
    bottom: false,
 
  });

  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };

  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
    <List>
    <ListItem>
        <Link  to='/swe363-project-react/'>
                        <Button color='inherit'>Home</Button>
                        
        </Link>
        </ListItem>
        <ListItem>

        <Link  to='/swe363-project-react/new-event'>
                        <Button color='inherit'>New Event</Button>
        </Link>
        </ListItem>

        <ListItem>

        <Link  to='/swe363-project-react/profile'>
                        <Button color='inherit'>Profile</Button>
        </Link>
        </ListItem>

        <ListItem>

        <Link  to='/swe363-project-react/history'>
                        <Button color='inherit'>History</Button>
        </Link>
        </ListItem>

        

        </List>

    </div>
  );


  return (
    <div>
      <Button onClick={toggleDrawer('right', true)}>|||</Button>
      
      <Drawer anchor="right" open={state.right} onClose={toggleDrawer('right', false)}>
        {sideList('right')}
      </Drawer>
      
  
    </div>
  );
}